import Vue from "vue";
import VueRouter from "vue-router";

// import Myorder from "../views/myorder.vue";
Vue.use(VueRouter);
// 组件按需加载  解决app启动首屏加载时间长
// component:resolve => require(["../views/plusaddress.vue"], resolve)
// component: () => import("../views/About.vue")
const routes = [
  {
    path: "/",
    redirect: "/home"
  },
  {
    path: "/plusaddress",
    name: "plusaddress",
    //       地址
    component: () => import("../views/plusaddress.vue")
  },
  {
    path: "/myorder",
    name: "myorder",

    component: () => import("../views/myorder.vue")
  },
  {
    path: "/order/:cartid",
    name: "order",
    component: () => import("../views/order.vue"),
    meta: {
      keepAlive: false
    }
    // beforeEnter: (to, from, next) => {
    //   //路由独享守卫
    //   console.log(to, from);
    //   if (from.name == "cart") {
    //     to.meta.keepAlive = false;
    //   }
    //   next();
    // }
  },
  {
    path: "/delivery",
    name: "delivery",
    component: () => import("../views/delivery.vue")
  },
  {
    path: "/address",
    name: "address",
    component: () => import("../views/address.vue")
  },
  {
    path: "/pay/:pay_sn", //支付页面需要pay_sn
    name: "pay",
    component: () => import("../views/pay.vue")
  },
  {
    path: "/choosePay",
    name: "choosePay",
    component: () => import("../views/choosePay.vue")
  },
  {
    path: "/home",
    name: "home",
    component: () => import("../views/Home.vue")
  },
  {
    path: "/categorylist",
    name: "categorylist",
    component: () => import("../views/categorylist.vue")
  },
  {
    path: "/category",
    name: "category",
    component: () => import("../views/category.vue")
  },
  {
    path: "/search",
    name: "search",
    component: () => import("../views/search.vue")
  },
  {
    path: "/info/:pid", //详情页设置商品id
    name: "info",
    component: () => import("../views/info.vue")
  },
  {
    path: "/surpirse",
    name: "surpirse",
    component: () => import("../views/surpirse.vue")
  },
  {
    path: "/cart",
    name: "cart",
    component: () => import("../views/cart.vue")
  },
  {
    path: "/my",
    name: "my",
    component: () => import("../views/my.vue"),
    beforeEnter: (to, from, next) => {
      //判断是否登录
      // const token = localStorage.token;
      // if (token) {
      //   next();
      // } else {
      //   next("/login");
      // }
      next();
    }
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../views/login.vue")
  },
  {
    path: "/reg",
    name: "reg",
    component: () => import("../views/reg.vue")
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  }
];

const router = new VueRouter({
  // mode: "history",//打包不使用 #/home     /home
  routes
});

export default router;
